import Vue from 'vue'
import Vuex from 'vuex' // 状态管理模式，管理全局变量

Vue.use(Vuex)

export default new Vuex.Store({ // Vuex存储数据的容器
  state: { // 存储的数据
    user: {
      userName: window.sessionStorage.getItem('user' || '[]') == null ? '' : JSON.parse(sessionStorage.getItem('user' || [])).name,
      password: window.sessionStorage.getItem('user' || []) == null ? '' : JSON.parse(sessionStorage.getItem('user' || [])).password
    }
  },
  mutations: { // 改变数据的方法
    login (state, user) {
      state.user.userName = user.name
      state.user.password = user.password
      window.sessionStorage.setItem('user', JSON.stringify(user)) // 将用户信息存储
    },
    logout (state) {
      state.user.userName = ''
      state.user.password = ''
      state.user = []
      console.log(state.user.userName)
      console.log(state.user.password)
      console.log(state.user)
      window.sessionStorage.removeItem('user')
    }
  }
})



